import {Component, Input, OnInit, Output, EventEmitter} from '@angular/core';
import {SocialActivity} from "../model/Activity";
import {SocialService} from "../social.service";

@Component({
    selector: 'social-discussion',
    templateUrl: './social-embed.component.html',
    styleUrls: [
        './social-embed.component.css'
    ]
})
export class SocialEmbedComponent implements OnInit {
    @Input() activityID: string;
    @Input() contextID: string;
    @Input() application: string;
    @Input() activity: SocialActivity;

    @Input() allowComment: boolean = true;

    @Output() onLoad = new EventEmitter<any>();
    @Output() onError = new EventEmitter<any>();

    totalComments: number = 0;

    constructor(
        private socialService: SocialService
    ) {}

    ngOnInit() {
        if (this.activity) {
            this._loaded();
        } else {
            this._loadActivity();
        }
    }

    private _loadActivity() {
        let params: any;
        if (this.activityID) {
            params = {
                activity: this.activityID
            };
        } else if (this.contextID && this.application) {
            params = {
                contextID: this.contextID,
                application: this.application
            };
        }

        if (params) {
            this.socialService.activityLoad(params, (activity, err) => {
                if (err) {
                    this.onError.emit(err);
                } else {
                    if (activity.errors) {
                        this.onError.emit(activity);
                    } else {
                        this.activity = activity;
                        this._loaded();
                    }
                }
            });
        } else {
            this.onError.emit({
                message: 'NOT_FOUND'
            });
        }
    }

    private _loaded() {
        this.onLoad.emit(this.activity);
    }
}
