import {
    Component,
    EventEmitter,
    Input,
    OnChanges,
    OnDestroy,
    OnInit,
    Output,
    SimpleChanges,
    ViewChild
} from '@angular/core';
import {SocialService} from '../social.service';
import {SocialActivity} from '../model/Activity';
import {SocialComment} from '../model/Comment';
import {Subscription} from "rxjs";
import {CommentPostComponent} from "../comment-post/comment-post.component";

@Component({
    selector: '[socialCommentList]',
    templateUrl: './comment-list.component.html',
    styleUrls: ['../comment/comment.component.css']
})
export class CommentListComponent implements OnInit, OnChanges, OnDestroy {
    @Input() activity: SocialActivity;
    @Input() allowComment: boolean = true;

    @Output() commentLoad = new EventEmitter<any>();

    @ViewChild(CommentPostComponent) commentPost: CommentPostComponent;

    remainComments: number;
    viewDate: string;

    private commentParams: any = {
        pageSize: 5,
        pageNumber: 0
    };

    private _commentChange: Subscription;

    constructor(
        protected socialService: SocialService
    ) {

        this.viewDate = this.socialService.viewDate;

        // Receive comment notify real time
        this._commentChange = this.socialService.commentChange.subscribe(activityId => {
            if (activityId === this.activity.uuid) {
                this.load(true);
            }
        });
    }

    ngOnInit() {
        // this.load(true);
    }

    ngOnChanges(changes: SimpleChanges) {
        this.load(true);
    }

    ngOnDestroy() {
        this._commentChange.unsubscribe();
    }

    focusForm() {
        if (this.commentPost) {
            this.commentPost.focusMessage();
        }
    }

    commentCreate(params: any) {
        params.activity = this.activity.uuid;
        this.socialService.commentPost(params, this.commentPosted.bind(this));
    }

    load(isInit?: boolean) {
        if (isInit) {
            this.commentParams.pageNumber = 0;
        } else {
            this.commentParams.pageNumber++;
        }

        this.commentParams.activity = this.activity.uuid;

        if (!this.activity.comments) {
            this.activity.comments = [];
        }

        this.socialService.commentWall(this.commentParams, data => {
            const comments = data && data['items'] || [];
            comments.forEach(comment => this.replaceOrAddComment(comment));

            this.activity.comments.sort(function (a, b) {
                return b.posted - a.posted;
            });

            this.remainComments =
                (data.total - (this.commentParams.pageNumber + 1) * this.commentParams.pageSize) || 0;

            this.commentLoad.emit(data);
        });
    }

    commentUpdated(comment: SocialComment) {
        this.replaceOrAddComment(comment);
    }

    commentDeleted(comment: SocialComment) {
        this.removeComment(comment.uuid);
    }

    private commentPosted(comment: SocialComment, err: any) {
        if (comment && comment.uuid && this.commentPost) {
            this.commentPost.resetForm();
            this.replaceOrAddComment(comment, true);
        }
    }

    private getIndexComment(uuid: string): number {
        for (let i = 0; i < this.activity.comments.length; i++) {
            if (this.activity.comments[i].uuid === uuid) {
                return i;
            }
        }
        return -1;
    }

    private replaceOrAddComment(comment: SocialComment, insertAtFirst?: boolean) {
        const index = this.getIndexComment(comment.uuid);
        if (index > -1) {
            this.activity.comments[index] = comment;
        } else {
            this.activity.comments[insertAtFirst ? 'unshift' : 'push'](comment);
        }
    }

    protected removeComment(uuid: string) {
        const index = this.getIndexComment(uuid);
        if (index > -1) {
            this.activity.comments.splice(index, 1);
        }
    }
}
