import {Component, ElementRef, Input, OnInit, ViewChild} from '@angular/core';
import {SocialService} from '../../social.service';
import {AccentService, CacheStorageService} from 'inet-core';

@Component({
    selector: '[socialListUser]',
    templateUrl: './list-user.component.html',
    styleUrls: [
        '../../styles/user-styles.css'
    ]
})
export class ListUserComponent implements OnInit {
    @Input() placeholder: string = 'TÃ¬m kiáº¿m...';
    @Input() height: string = '240px';

    @ViewChild('search') search: ElementRef;

    members: any[] = [];
    selectMembers: any = [];
    showMembers: any = [];
    matchMembers: any = [];

    private _search: HTMLInputElement;
    private _allEmployees = 'social_employees';
    private _allFriends = 'social_friends';

    constructor(
        private socialService: SocialService,
        private accentService: AccentService,
        private cacheStorage: CacheStorageService
    ) {

        const employees = this.cacheStorage.getData(this._allEmployees);
        // Cache employees on 5 minutes
        if (employees) {
            this._initMembers(JSON.parse(employees));
        } else {
            this.cacheStorage.promiseQueue(this._allEmployees,
                (key) => {
                    this.socialService.searchMember({pageSize: -1}, (users) => {
                        if (users && users.length > 0) {
                            this.cacheStorage.setData(key, JSON.stringify(users));
                        }
                        this.cacheStorage.resolveQueue(key, this, [users]);
                    });
                },
                (users) => this._initMembers(users));
        }

        this.cacheStorage.promiseQueue(this._allFriends,
            (key) => {
                this.socialService.listFriends({pageSize: -1}, (users) => {
                    this.cacheStorage.resolveQueue(key, this, [users]);
                });
            },
            (users) => this._initMembers(users));
    }

    ngOnInit() {
        this._search = this.search.nativeElement;
    }

    resetData() {
        this.selectMembers = [];
        this.showMembers = [];
        this.matchMembers = [];
        this._search.value = '';
        this.members.forEach(item => delete item.selected);
    }

    focusInput() {
        this._search.focus();
    }

    onSearch() {
        let value = this._search.value.trim();
        if (value) {
            value = this.accentService.viToEn(value.toLowerCase());
            this.matchMembers = this.members.filter(item => item._index.indexOf(value) > -1);
        } else {
            this.matchMembers = this.members;
        }

        this.showMembers = this.matchMembers;
    }

    onScroll() {

    }

    addMember(member) {
        member.selected = !member.selected;
        if (member.selected) {
            this.selectMembers.push(member);
        } else {
            this.removeMember(member);
        }
    }

    removeMember(member) {
        this.selectMembers.splice(this.selectMembers.indexOf(member), 1);
    }

    private _initMembers(users: any[]) {
        users.forEach(item => this._addMember(item));
        this._sortMembers();
    }

    private _addMember(member) {
        member.username = member.username || member.usercode;
        if (this._hasMember(member)) {
            return;
        }
        member._index = this.accentService.viToEn(member.fullname.toLowerCase()) + ' ' + member.username;
        this.members.push(member);
    }

    private _sortMembers() {
        this.members.sort((a, b) => a.fullname.localeCompare(b.fullname));
    }

    private _hasMember(member) {
        for (let i = 0; i < this.members.length; i++) {
            if (this.members[i].username === member.username) {
                return true;
            }
        }
    }
}
