import {AfterViewInit, Component, ElementRef, OnDestroy, ViewChild} from '@angular/core';
import {SocialActivity} from '../model/Activity';
declare let $: any;

@Component({
  selector: 'app-social-modal-view',
  templateUrl: './modal-view.component.html',
  styleUrls: ['./modal-view.component.css']
})
export class SocialModalViewComponent implements AfterViewInit, OnDestroy {
  @ViewChild('viewModal') viewModal: ElementRef;
  activity: SocialActivity;
  $modal: any;

  constructor() { }

  ngAfterViewInit() {
    this.$modal = $(this.viewModal.nativeElement);

    this.$modal.on('hidden.bs.modal', (e) => {
      delete this.activity;
    });

    $(document.body).append(this.$modal);
  }

  ngOnDestroy() {
    this.$modal.remove();
  }

  viewActivity(activity: SocialActivity) {
    this.activity = activity;
    setTimeout(() => {
      this.$modal.modal('show');
    }, 100);
  }
}
