import {
    Component,
    ElementRef,
    ViewChild,
    AfterViewInit,
    OnDestroy,
    OnInit,
    Input
} from '@angular/core';
import {SocialService} from '../social.service';
import {
    Policy, SHARE_COMPANY, SHARE_FRIEND, SHARE_ME,
    SHARE_PUBLISH, SocialMember
} from '../model/Config';
import {AccentService} from 'inet-core';
import {SocialActivity} from "../model/Activity";
import {SocialComment} from "../model/Comment";

@Component({
    selector: '[socialShareMember]',
    templateUrl: './share-member.component.html',
    styleUrls: [
        '../styles/user-styles.css',
        './share-member.component.css'
    ]
})
export class ShareMemberComponent implements OnInit, OnDestroy, AfterViewInit {
    @ViewChild('shareModal') shareModal;
    @ViewChild('searchInput') inputMember: ElementRef;
    @ViewChild('contactList') contactList: ElementRef;

    @Input() companyMode: boolean = true;
    @Input() activity: SocialActivity | SocialComment;

    categories: SocialMember[];

    companyCategories: SocialMember[] = [
        SHARE_COMPANY,
        SHARE_ME
    ];

    communityCategories: SocialMember[] = [
        SHARE_PUBLISH,
        SHARE_FRIEND,
        SHARE_ME
    ];

    groups: SocialMember[] = [];

    // All groups, members
    contacts: SocialMember[] = [];

    // Groups, members active
    members: SocialMember[] = [];

    friends: SocialMember[] = [];

    // Search data
    searchContacts: SocialMember[] = [];
    selectContacts: SocialMember[] = [];

    showMenu: boolean = false;

    displayIcon: string;
    display: string;
    policies: Policy[] = [];

    customActive: boolean = false;

    private _matchContacts: SocialMember[] = [];
    private _params = {
        pageSize: 10,
        pageNumber: 0,
        loading: false
    };
    private _modalShowObserver;

    constructor(
        private socialService: SocialService,
        private accentService: AccentService
    ) { }

    ngOnInit() {
        document.body.appendChild(this.shareModal._element.nativeElement);

        this.onCloseMenu = this.onCloseMenu.bind(this);
        document.addEventListener('click', this.onCloseMenu);

        this.init();

        setTimeout(() => {
            if (this.activity) {
                this.setPolicies(this.activity._policies);
            } else {
                this.setPolicies();
            }
        }, 100);
    }

    ngOnDestroy() {
        document.removeEventListener('click', this.onCloseMenu);
        this._modalShowObserver.unsubscribe();
    }

    ngAfterViewInit() {
        this._modalShowObserver = this.shareModal.onShown.subscribe(() => {
            this._focusInputMember();
            this.searchMember();
        });
    }

    init() {

        if (this.companyMode) {
            this.categories = this.companyCategories;

            // Load all users company
            let userAdded = [];
            this.socialService.searchMember({pageSize: -1}, (members) => {
                members.forEach(member => {
                    let user = <SocialMember>this._createSocialMember({
                        member: member.username,
                        display: member.fullname,
                        policy: Policy.Individual
                    });
                    user.username = member.username;
                    this.contacts.push(user);
                });

                this._sortMembers(this.contacts);

            });

        } else {
            this.categories = this.communityCategories;

            // Load all groups
            this.socialService.listGroups((groups) => {
                groups.forEach(group => {
                    let member = this._createSocialMember({
                        display: group,
                        member: group,
                        policy: Policy.Friend
                    });
                    member.group = true;
                    this.groups.push(member);
                    this.contacts.push(member);
                });

                this._sortMembers(this.groups);
                this._sortMembers(this.contacts);
            });

            // Load all friends
            this.socialService.listFriends({pageSize: -1}, (members) => {
                members.forEach(member => {
                    let user = this._createSocialMember({
                        member: member.usercode,
                        display: member.fullname,
                        policy: Policy.Individual
                    });
                    user.username = member.usercode;
                    this.friends.push(user);
                    this.contacts.push(user);
                });

                this._sortMembers(this.friends);
                this._sortMembers(this.contacts);

            });
        }

        this.categories.forEach(category => category._index = this._indexKeyToSearch(category.display));
        this.contacts = this.categories.slice();

    }

    setDefault() {
        this._resetActive();
        this._resetModal();
        this.closeMenu();
    }

    setPolicies(members?: SocialMember[]) {

        this.setDefault();

        if (!members || members.length < 1) {
            members = [this.categories[0]];
        }

        // Set active category or group
        if (members.length === 1 && (members[0].category || members[0].group)) {
            members[0].active = true;
        } else {
            this.customActive = true;
        }

        let displays = [], policies = [];
        for (let i = 0; i < members.length; i++) {
            let member = members[i];
            displays.push(member.display);
            policies = policies.concat(member.value);
        }

        this.policies = policies;
        this.display = displays.join(', ');
        this.displayIcon = members[0].icon;
        this.members = members;

    }

    injectPolicies(params: any) {
        if (this.policies.length < 1) {
            params.published = true;
            // params.element = JSON.stringify('[]');
        } else {
            params.element = JSON.stringify(this.policies);
        }
        return params;
    }

    selectMember(member: SocialMember) {
        this.setPolicies([member]);
        this.closeMenu();
    }

    onCloseMenu() {
        if (!this.isOpening()) {
            this.showMenu = false;
        }
    }

    openSelectCustom() {
        this.selectContacts = this.members.slice();
        this.shareModal.show();
        this._updateSelectContacts();
    }

    closeMenu() {
        this.showMenu = false;
    }

    openMenu() {
        this.showMenu = true;
    }

    searchMember() {
        let value = this._indexKeyToSearch(this._getSearchKeyword());
        if (value) {
            this._matchContacts = this.contacts.filter(member => {
                return member._index.indexOf(value) > -1 ||
                    (typeof member.id === 'string' && member.id.indexOf(value) > -1);
            });
        } else {
            this._matchContacts = this.contacts;
        }
        this._loadContacts(true);
    }

    addMember(member: SocialMember) {
        member.selected = !member.selected;
        if (member.selected) {
            if (!this.hasSelectedMember(member)) {
                this.selectContacts.push(member);
            }
        } else {
            this.removeMember(member);
        }
    }

    removeMember(member: SocialMember) {
        let index = this.getIndexMemberSelected(member);
        if (index > -1) {
            member.selected = false;
            this.selectContacts.splice(index, 1);
        }
    }

    hasSelectedMember(member: SocialMember): boolean {
        return this.getIndexMemberSelected(member) > -1;
    }

    getIndexMemberSelected(member: SocialMember) {
        for (let i = 0; i < this.selectContacts.length; i++) {
            if (this.selectContacts[i].id === member.id) {
                return i;
            }
        }
        return -1;
    }

    isOpening() {
        return this.shareModal.isShown;
    }

    cancelChanges(e) {
        e.stopPropagation();
        this.shareModal.hide();
    }

    saveChanges(e) {
        e.stopPropagation();
        this.setPolicies(this.selectContacts);
        this.shareModal.hide();
        this.closeMenu();
    }

    onScrollContact(e) {
        let node = e.target;
        if (!this._params.loading && node.scrollHeight - (node.scrollTop + node.clientHeight) <= 40) {
            this._loadContacts();
        }
    }

    private _scrollTopModal() {
        this.contactList.nativeElement.scrollTop = 0;
    }

    private _updateSelectContacts() {
        this.contacts.forEach(member => member.selected = this.hasSelectedMember(member));
    }

    private _resetActive() {
        this.categories.forEach(cat => cat.active = false);
        this.groups.forEach(group => group.active = false);
        this.customActive = false;
    }

    private _resetModal() {
        this._params.loading = false;
        this._params.pageNumber = 0;
        this._matchContacts = [];
        this.selectContacts = [];
        this.searchContacts = [];
        this._setSearchKeyword('');
    }

    private _loadContacts(init?: boolean) {
        this._params.loading = true;
        if (init) {
            this._scrollTopModal();
            this._params.pageNumber = 0;
            this.searchContacts = [];
        }
        this.searchContacts = this.searchContacts.concat(this._matchContacts.slice(
            this._params.pageNumber * this._params.pageSize,
            (this._params.pageNumber + 1) * this._params.pageSize));

        this._params.pageNumber += 1;
        this._params.loading = !this._hasContacts();
    }

    private _hasContacts() {
        return this._matchContacts.length > this._params.pageNumber * this._params.pageSize;
    }

    private _sortMembers(members: SocialMember[]) {
        members.sort((a, b) => a.display.localeCompare(b.display));
    }

    private _indexKeyToSearch(str: string): string {
        return this.accentService.viToEn(str).toLowerCase();
    }

    private _createSocialMember(policyItem): SocialMember {
        return {
            id: policyItem.member,
            display: policyItem.display,
            icon: this.socialService.getIconByType(policyItem.policy),
            value: [policyItem],
            _index: this._indexKeyToSearch(policyItem.display)
        };
    }

    private _focusInputMember() {
        this.inputMember.nativeElement.focus();
    }

    private _setSearchKeyword(value: string) {
        this.inputMember.nativeElement.value = value;
    }

    private _getSearchKeyword() {
        return this.inputMember.nativeElement.value;
    }
}
