import {Component, OnInit} from '@angular/core';
import {SocialFriend} from '../../model/SocialFriend';
import {SocialService} from '../../social.service';

@Component({
    templateUrl: './friend-invitation.component.html',
    styleUrls: [
        '../style.css',
        './friend-invitation.component.css'
    ]
})
export class FriendInvitationComponent implements OnInit {

    // Default group
    category: string = 'Friends';

    invitations: SocialFriend[];
    noInvitation: boolean;

    invitationsSend: SocialFriend[];
    noInvitationSend: boolean;

    constructor(
        private socialService: SocialService
    ) {
    }

    ngOnInit() {
        this._loadReceiveInvite();
        this._loadSendInvite();
    }

    acceptInvite(member) {
        this.socialService.invitationAccept({
            member: member.inviter,
            category: member.category
        }, (response, error) => {

        });
        this.invitations.splice(this.invitations.indexOf(member), 1);
        this.noInvitation = this.invitations.length < 1;
    }

    rejectInvite(member) {
        this.socialService.invitationReject({
            member: member.inviter,
            category: member.category
        }, (response, error) => {

        });
        this.invitations.splice(this.invitations.indexOf(member), 1);
        this.noInvitation = this.invitations.length < 1;
    }

    removeInvite(member) {
        this.socialService.invitationDelete({
            member: member.usercode,
            category: member.category
        }, (response, error) => {

        });
        this.invitationsSend.splice(this.invitationsSend.indexOf(member), 1);
        this.noInvitationSend = this.invitationsSend.length < 1;
    }

    private _loadReceiveInvite() {
        this.socialService.invitationReceiveList({pageSize: -1}, (respone, error) => {
            this.invitations = respone && respone.items || [];
            this.noInvitation = this.invitations.length < 1;
        });
    }

    private _loadSendInvite() {
        this.socialService.invitationSendList({pageSize: -1}, (respone, error) => {
            this.invitationsSend = respone && respone.items || [];
            this.noInvitationSend = this.invitationsSend.length < 1;
        });
    }
}
