import {Component, Input, OnInit} from '@angular/core';
import {SocialFriend} from '../../model/SocialFriend';
import {SocialService} from '../../social.service';
import {FriendTab} from '../FriendTab';

@Component({
    templateUrl: './friend-browse.component.html',
    styleUrls: [
        '../style.css',
        './friend-browse.component.css'
    ]
})
export class FriendBrowseComponent implements OnInit, FriendTab {
    category: string = 'All friends';
    members: SocialFriend[] = [];

    params: any = {
        pageSize: 20,
        pageNumber: 0
    };

    private _timer;

    constructor(
        private socialService: SocialService
    ) {
    }

    placeholder: string = 'TÃ¬m kiáº¿m báº¡n bÃ¨...';

    clearSearchValue(): void {
    }

    onSearch(value: string): void {
        this._searchMember(value);
    }

    ngOnInit() {
        this._searchMember('');
    }

    inviteFriend(friend) {
        friend.added = true;
        this.socialService.invitationCreate({
            member: friend.username,
            memberName: friend.fullname,
            type: 'FRIEND',
            category: this.category
        }, (response, error) => {
        });
    }

    private _searchMember(keyword: string) {
        if (this.params.keyword === keyword) {
            return;
        }
        this.params.keyword = keyword;
        this.params.pageNumber = 0;

        clearTimeout(this._timer);
        this._timer = setTimeout(() => {
            this.socialService.invitationSearch(this.params, (respone, error) => {
                if (this.params.keyword != keyword) {
                    return;
                }
                const members = respone || [];
                const userCodes = [];
                this.members = [];
                members.forEach(item => {
                    if (userCodes.indexOf(item.username) < 0) {
                        userCodes.push(item.username);
                        if (!item.fullname) {
                            item.fullname = item.username;
                        }
                        this.members.push(item);
                    }
                });
            });
        }, 500);
    }
}
