import {Component, EventEmitter, Input, Output} from '@angular/core';
import {SocialComment} from '../model/Comment';
import {SocialService} from '../social.service';

@Component({
    selector: '[socialComment]',
    templateUrl: './comment.component.html',
    styleUrls: ['./comment.component.css']
})
export class CommentComponent {
    @Input() comment: SocialComment;
    @Input() editable: boolean = true;

    @Output() onUpdated = new EventEmitter<SocialComment>();
    @Output() onDeleted = new EventEmitter<SocialComment>();

    viewDate: string;
    editing: boolean;

    constructor(
        private socialService: SocialService
    ) {
        this.viewDate = this.socialService.viewDate;
    }

    commentCancelEdit() {
        this.editing = false;
    }

    commentEdit() {
        this.editing = true;
    }

    commentUpdate(params) {
        this.socialService.commentUpdate(params, (comment: SocialComment) => {
            if (comment && comment.uuid) {
                this.commentCancelEdit();
                this.onUpdated.emit(comment);
            }
        });
    }

    commentDelete() {
        this.socialService.commentRemove({
            comment: this.comment.uuid,
            activity: this.comment.activityID
        }, (comment: SocialComment) => {
            if (comment && comment.uuid) {
                this.onDeleted.emit(comment);
            }
        });
    }

    commentLike() {
        const params = {
            activity: this.comment.activityID,
            comment: this.comment.uuid,
        };
        this.comment.likeStatus = !this.comment.likeStatus;
        if (this.comment.likeStatus) {
            this.comment.likeCount++;
            this.socialService.commentLike(params, data => {});
        } else {
            this.comment.likeCount--;
            this.socialService.commentUnLike(params, data => {});
        }
    }
}
