import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {SocialActivity} from '../model/Activity';
import {SocialComment} from '../model/Comment';

@Component({
    selector: '[actionMore]',
    templateUrl: './activity-action-more.component.html',
    styleUrls: ['./activity-action-more.component.css']
})
export class ActivityActionMoreComponent implements OnInit {
    @Input() data: SocialActivity | SocialComment;
    @Output() onEdit = new EventEmitter<any>();
    @Output() onDelete = new EventEmitter<any>();

    hasAction: boolean;
    isActivity: boolean;

    constructor() {
    }

    ngOnInit() {
        if (!this.data) {
            return;
        }
        this.isActivity = !this.data.activityID;
        this.hasAction = this.isActivity && this.data._editable;
    }
}
