import {Component, Input, OnChanges, SimpleChanges} from '@angular/core';
import {SocialService} from '../social.service';
import {SocialActivity} from '../model/Activity';

@Component({
  selector: 'social-activity-view',
  templateUrl: './activity-view.component.html',
  styleUrls: ['./activity-view.component.css']
})
export class ActivityViewComponent implements OnChanges {
  @Input() activityId: string;

  activity: SocialActivity;
  notFound: boolean;
  constructor(
    private socialService: SocialService
  ) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.activityId.currentValue !== changes.activityId.previousValue) {
      this._load();
    }
  }

  activityUpdated(activity: SocialActivity) {
    this.activity = activity;
  }

  private _load() {
    this.notFound = false;
    this.socialService.activityLoad({
      activity: this.activityId
    }, (activity: SocialActivity) => {
      if (activity && activity.uuid) {
        this.activity = activity;
      } else {
        this.notFound = true;
      }
    });
  }
}
