import {Component, Input, OnInit} from '@angular/core';
import {SocialActivity} from "../model/Activity";
import {SocialService} from "../social.service";
import {SocialComment} from "../model/Comment";
import {Router} from "@angular/router";

@Component({
    selector: 'social-latest',
    templateUrl: './social-latest.component.html',
    styleUrls: ['./social-latest.component.css']
})
export class SocialLatestComponent implements OnInit {
    @Input() viewMoreEnable: boolean = true;

    activities: SocialActivity[] = [];

    constructor(private socialService: SocialService, private router: Router) {}

    ngOnInit() {
        this._loadLatest();
    }

    viewActivity(activity: SocialActivity) {
        // this.onViewItem.emit(activity);
        this.router.navigate(['social/activity/' + (activity['activityID'] || activity['uuid'])]);
    }

    viewMore() {
        // this.onViewMore.emit();
        this.router.navigate(['social']);
    }

    private _loadLatest() {
        this.socialService.activityWall({
            pageSize: 3
        },  (result) => {
            const items = result && result['items'] || [];
            if (items.length > 0) {
                this._updateWithCommentLatest(items, () => {
                    items.sort((a, b) => (b.lastUpdate || b.posted) - (a.lastUpdate || a.posted));
                    this.activities = items;
                });
            }
        });
    }

    private _updateWithCommentLatest(activities: SocialActivity[], callback: Function) {
        const promises = [];
        activities.forEach((activity, index) => {
            promises.push(new Promise((resolve) => {
                this.socialService.commentWall({pageSize: 1, activity: activity.uuid}, (result) => {
                    const items: SocialComment[] = result && result['items'] || [];
                    if (items.length > 0) {
                        // Replace activity by latest comment
                        activities[index] = items[0];
                    }
                    resolve();
                });
            }));
        });
        Promise.all(promises).then(() => callback());
    }
}
