import {Component, OnDestroy, OnInit} from '@angular/core';
import {ActivatedRoute, NavigationEnd, Router} from "@angular/router";
import {filter} from "rxjs/operators";

@Component({
  templateUrl: './social-activity-view.component.html',
  styleUrls: ['../home/social-home.component.css']
})
export class SocialActivityViewComponent implements OnInit, OnDestroy {
  activityId: string;
  private _routerObserver;

  constructor(
      private route: ActivatedRoute,
      private router: Router
  ) { }

  ngOnInit() {
    this._routerObserver = this.router.events
        .pipe(filter(event => event instanceof NavigationEnd))
        .subscribe(() => this._updateActivityId());
    this._updateActivityId();
  }

  ngOnDestroy() {
    this._routerObserver.unsubscribe();
  }

  private _updateActivityId() {
    const params = this.route.snapshot.paramMap;
    if (params.get('id')) {
      this.activityId = params.get('id');
    }
  }
}
