import {Component, OnDestroy, OnInit, TemplateRef, ViewChild} from '@angular/core';
import {SocialService} from "../../social.service";
import {NavigationEnd, Router} from "@angular/router";
import {GroupMemberComponent} from "../group-member/group-member.component";
import {GroupCreateComponent} from "../group-create/group-create.component";
import {BsModalService} from 'ngx-bootstrap';
import {FriendTab} from '../FriendTab';
import {AccentService} from 'inet-core';
import {filter} from "rxjs/operators";

@Component({
  templateUrl: './group.component.html',
  styleUrls: [
    '../style.css',
    './group.component.css'
  ]
})
export class SocialGroupComponent implements OnInit, OnDestroy, FriendTab {
  @ViewChild('modalCreateMember') modalCreateMember: GroupCreateComponent;
  @ViewChild('modalAddMember') modalAddMember: GroupMemberComponent;

  viewGroups: boolean;
  groups: string[];
  friends: any[] = [];

  viewMembers: boolean;
  members: any[] = [];
  groupView: string;
  groupActive: string;
  memberActive: any;

  modalGroupDeleteRef: any;
  modalMemberDeleteRef: any;

  noGroups: boolean;
  noFriends: boolean;
  searching: boolean;

  private _prefix = '/social/friend';
  private _router;

  constructor(
    private socialService: SocialService,
    private accentService: AccentService,
    private router: Router,
    private modalService: BsModalService
  ) {
    this._loadAllFriends();
  }

  placeholder = 'TÃ¬m kiáº¿m...';
  onSearch(value: string) {
    this.searching = !!value;
    if (value) {
      value = this.accentService.viToEn(value);
      if (this.viewGroups) {
        this.friends.forEach(item => {
          item.hidden = item._index.indexOf(value) < 0;
        });
      } else {
        this.members.forEach(item => {
          item.hidden = item._index.indexOf(value) < 0;
        });
      }
    } else {
      if (this.viewGroups) {
        this.friends.forEach(item => delete item.hidden);
      } else {
        this.members.forEach(item => delete item.hidden);
      }
    }
  }
  clearSearchValue() { }

  ngOnInit() {
    this._router = this.router.events
      .pipe(filter(event => event instanceof NavigationEnd))
      .subscribe(() => {
        this._actionView();
      });
    this._actionView();
  }

  ngOnDestroy() {
    this._router.unsubscribe();
  }

  createGroup() {
    this.modalCreateMember.open();
  }

  groupCreated() {
    this._loadGroups();
    this._loadAllFriends();
  }

  viewGroup(group: string) {
    this.router.navigate([this._prefix + '/' + group]);
  }

  removeGroup(group: string, template: TemplateRef<any>) {
    this.groupActive = group;
    this.modalGroupDeleteRef = this.modalService.show(template);
  }

  // Todo: improve remove group
  onDeleteGroup() {
    this.modalGroupDeleteRef.hide();
    this._loadFriends(this.groupActive, (members) => {
      this.socialService.groupMemberDelete({
        category: this.groupActive,
        member: this._getMemberStr(members)
      }, () => {
        this._loadGroups();
        this._loadAllFriends();
        if (!this.viewGroups) {
          this.toGroups();
        }
      });
    });
    this.groups.splice(this.groups.indexOf(this.groupActive), 1);
  }

  removeFriend(friend, template: TemplateRef<any>) {
    this.modalMemberDeleteRef = this.modalService.show(template);
    this.memberActive = friend;
  }

  onDeleteMember() {
    const promises = [];
    if (this.memberActive.categories) {
      // Member on groups
      this.memberActive.categories.forEach(category => {
        promises.push(this._deleteMemberPromise(this.memberActive.usercode, category));
      });
    } else {
      promises.push(this._deleteMemberPromise(this.memberActive.usercode, this.memberActive.category));
    }

    Promise.all(promises).then(values => {
      if (this.groupView) {
        this._loadMembers();
      }
      this._loadAllFriends();
    });
    this.modalMemberDeleteRef.hide();
  }

  private _deleteMemberPromise(usercode, category) {
    return new Promise((resolve, reject) => {
      this.socialService.groupMemberDelete({
        category: category,
        member: usercode
      }, resolve);
    });
  }

  addMemberGroup() {
    this.modalAddMember.open();
  }

  saveMemberGroup(members) {
    this.socialService.groupMemberCreate({
      category: this.groupView,
      member: this._getMemberStr(members)
    }, () => {
      this._loadMembers();
      this._loadAllFriends();
    });
  }

  toGroups() {
    this.router.navigate([this._prefix]);
  }

  private _viewGroup() {
    this.viewGroups = true;
    this.groupView = '';
    if (!this.groups) {
      this._loadGroups();
    }
  }

  private _loadGroups() {
    this.noGroups = false;
    this.socialService.listGroups(groups => {
      this.groups = groups;
      this.noGroups = groups.length < 1;
    });
  }

  private _viewGroupMember(group: string) {
    this.viewMembers = true;
    this.members = [];
    this.groupView = group;
    this._loadMembers();
  }

  private _loadMembers() {
    this._loadFriends(this.groupView, users => this.members = users);
  }

  private _loadAllFriends() {
    this.friends = [];
    this.noFriends = false;
    this._loadFriends('', users => {
      const tmp = [];
      users.forEach(item => {
        const index = tmp.indexOf(item.usercode);
        if (index < 0) {
          tmp.push(item.usercode);
          item.categories = [item.category];
          this.friends.push(item);
        } else {
          this.friends[index].categories.push(item.category);
        }
      });
      this.noFriends = this.friends.length < 1;
    });
  }

  private _loadFriends(category: string, callback) {
    this.socialService.listFriends({
      pageSize: -1,
      category: category
    }, (users) => {
      users.forEach(item => {
        item._index = this._indexSearch(item.fullname) + ' ' + item.usercode;
      });
      callback(users);
    });
  }

  private _getMemberStr(members: any[]): string {
    return members.map(item => item.usercode || item.username).join(',');
  }

  private _actionView() {
    this.viewGroups = this.viewMembers = false;
    const group = this.router.url.split('/')[3];

    this.clearSearchValue();

    if (!group) {
      this._viewGroup();
    } else {
      this._viewGroupMember(decodeURIComponent(group));
    }
  }

  private _indexSearch(value: string) {
    return this.accentService.viToEn(value.toLowerCase());
  }
}
