import {
    Component,
    EventEmitter,
    Output,
    Input,
    ViewChild,
    ElementRef,
    OnInit,
    OnDestroy
} from '@angular/core';
import {SocialActivity} from '../model/Activity';
import {SocialComment} from '../model/Comment';
import {ShareMemberComponent} from '../share-member/share-member.component';
import {AutoSizeDirective} from 'inet-core';
import {SocialService} from "../social.service";
import {ProgressComponent} from "../progress/progress.component";
import {LinkPreviewComponent, FileListItem} from "inet-ui";
declare let $: any;

@Component({
    selector: '[activityPost]',
    templateUrl: './activity-post.component.html',
    styleUrls: ['./activity-post.component.css']
})

export class ActivityPostComponent implements OnInit, OnDestroy {
    @Input() activity: SocialActivity;
    @Input() shareMemberEnable: boolean = false;
    @Input() linkPreviewEnable: boolean = false;
    @Input() enterPost: boolean = false;
    @Input() showWithAnimation: boolean = false;
    @Input() placeHolder: string = 'Ná»i dung chia sáº»...';
    @Input() companyMode: boolean = true;

    @Output() onCancel = new EventEmitter<any>();
    @Output() onPost = new EventEmitter<any>();

    @ViewChild('postElement') postElement: ElementRef;
    @ViewChild('messageEl') messageEl: ElementRef;
    @ViewChild(ShareMemberComponent) shareMember: ShareMemberComponent;
    @ViewChild(AutoSizeDirective) messageAutoSize: AutoSizeDirective;
    @ViewChild(LinkPreviewComponent) linkPreview: LinkPreviewComponent;
    @ViewChild(ProgressComponent) indicator: ProgressComponent;

    message: string = '';
    files: FileListItem[] = [];
    removeFiles: FileListItem[] = [];

    // Detect link with space at end "http://innetcloud.vn "
    private linkRegex = /(https?:\/\/[^\s]+)/;
    private postEl;
    private _clickOut: boolean = false;

    constructor(private socialService: SocialService) { }

    ngOnInit() {
        this.postEl = this.postElement.nativeElement;

        // on paste detect link
        this.messageEl.nativeElement.addEventListener('paste', () => {
            setTimeout(() => {
                this.detectLink();
            }, 0);
        });

        this.focusOutHideForm = this.focusOutHideForm.bind(this);
        document.addEventListener('click', this.focusOutHideForm);

        if (this.activity) {
            this.editActivity(this.activity);
        }
    }

    ngOnDestroy() {
        document.removeEventListener('click', this.focusOutHideForm);
    }

    focusOutHideForm(e) {
        // Click inside
        if (e.target.isSameNode(this.postEl) || this.postEl.contains(e.target)) {
            return;
        }
        if (this._clickOut) {
            return;
        }

        this._clickOut = true;
        if (!this.isFocusForm() && !this.hasData() && !this.isShareMemberOpening()) {
            this.closeForm();
        }
        setTimeout(() => {
            this._clickOut = false;
        }, 500);
    }

    isFocusForm() {
        return $(this.postEl).find(':focus').length > 0;
    }

    enterPostForm(e) {
        if (this.enterPost) {
            e.preventDefault();
            this.postActivity();
        }
    }

    postActivity() {
        const params = this.getData();
        if (!params.message) {
            this.focusMessage();
            return;
        }
        this.onPost.emit(params);
        this.indicator.show();
    }

    getData() {
        let params: any = {
            message: this.message
        };

        // File upload
        for (let i = 0; i < this.files.length; i++) {
            if (!this.files[i].id) {
                params['file-' + i] = this.files[i];
            }
        }

        // File removed
        const fileRemoved = this.getUuidFileRemoved();
        if (fileRemoved) {
            params.gridfsUUID = fileRemoved;
        }

        if (this.activity) {
            params.activity = this.activity.uuid;
        }

        if (this.shareMember) {
            this.shareMember.injectPolicies(params);
        }

        if (this.linkPreview && this.linkPreview.getData()) {
            params.openGraph = this.linkPreview.getData();
        }

        return params;
    }

    hasData() {
        return this.message || this.files.length;
    }

    editActivity(activity: SocialActivity | SocialComment) {
        this.resetForm();

        this.activity = activity;

        this.message = activity.message;

        this.files = this.socialService.attachmentToFiles(activity.attachments);

        this.focusMessage();

        // if (this.shareMember) {
        //     this.shareMember.setPolicies(activity._policies);
        // }

        if (activity._openGraph && this.linkPreview) {
            this.linkPreview.setData(activity._openGraph);
        }

        this.showForm(true);

        // Trigger input to adjust size
        this.resizeInput();

    }

    resetForm() {

        // Clear data
        this.message = '';
        this.files.length = 0;
        this.removeFiles.length = 0;

        if (this.linkPreview) {
            this.linkPreview.clearData();
        }

        if (this.indicator) {
            this.indicator.hide();
        }

        this.resizeInput();
    }

    typeMessage() {
        if (this.message.trim() && this.message.slice(-1) === ' ') {
            this.detectLink();
        }
    }

    closeForm() {
        this.resetForm();
        this.hideForm(true);

        if (this.activity) {
            this.onCancel.emit(this.activity);
            this.activity = null;
        }
    }

    showForm(noTransition?: boolean) {
        if (this.postEl.isShow) {
            return;
        }

        if (!this.showWithAnimation && !noTransition) {
            noTransition = true;
        }

        if (noTransition) {
            this.setFormHeight('auto');
            this.postEl.style.overflow = 'visible';
        } else {
            this.setPostHeight();
            setTimeout(() => {
                this.setFormHeight('auto');
                this.postEl.style.overflow = 'visible';
            }, 400);
        }
        this.focusMessage();
        this.postEl.isShow = true;
    }

    hideForm(noTransition?: boolean) {
        if (!this.postEl.isShow) {
            return;
        }

        if (!this.showWithAnimation && !noTransition) {
            noTransition = true;
        }

        if (noTransition) {
            this.setFormHeight('');
        } else {
            this.setPostHeight();
            setTimeout(() => {
                this.setFormHeight('');
            }, 0);
        }
        this.postEl.style.overflow = 'hidden';
        this.postEl.isShow = false;
    }

    isShareMemberOpening() {
        return this.shareMember && this.shareMember.isOpening();
    }

    focusMessage() {
        this.messageEl.nativeElement.focus();
    }

    resizeInput() {
        setTimeout(() => {
            this.messageAutoSize.adjust();
        }, 0);
    }

    private detectLink() {
        if (!this.linkPreviewEnable || !this.linkPreview) {
            return;
        }

        // Only one link preview
        if (this.linkPreview.isPreview()) {
            return;
        }

        let links = this.linkRegex.exec(this.message);
        if (links && links.length > 0) {
            this.linkPreview.setLink(links[0]);
        }
    }

    private setPostHeight() {
        this.setFormHeight(this.postEl.scrollHeight + 'px');
    }

    private setFormHeight(height: string) {
        this.postEl.style.height = height;
    }

    private getUuidFileRemoved(): string {
        return this.removeFiles.filter(file => file.id).map(file => file.id).join(',');
    }
}
