import {Component, ComponentRef, OnInit, ViewChild, ViewContainerRef} from '@angular/core';
import {FormControl} from '@angular/forms';
import {Router} from "@angular/router";
import {NavigationData} from "inet-ui";
import {SocialGroupComponent} from "../../social-friend/group/group.component";
import {FriendInvitationComponent} from "../../social-friend/invitation/friend-invitation.component";
import {FriendBrowseComponent} from "../../social-friend/browse/friend-browse.component";
declare let $: any;

@Component({
    templateUrl: './social-friend.component.html',
    styleUrls: ['./social-friend.component.css']
})
export class SocialFriendComponent implements OnInit {

    @ViewChild('tabViewContainer', {read: ViewContainerRef}) _tabViewContainer;
    @ViewChild('searchEl') searchEl;

    tabs: NavigationData[] = [
        {
            name: 'Trang chá»§',
            component: '',
            path: ''
        },
        {
            name: 'Báº¡n bÃ¨',
            component: SocialGroupComponent,
            path: '/friend'
        },
        {
            name: 'Lá»i má»i',
            component: FriendInvitationComponent,
            path: '/friend/invitation'
        },
        {
            name: 'TÃ¬m báº¡n',
            component: FriendBrowseComponent,
            path: '/friend/browse'
        }
    ];

    searchValue: string = '';
    search = new FormControl();
    placeholder: string;

    prefix: string;

    private _activeComponent;
    private _searchEl;
    private _searchable: boolean;

    constructor(private router: Router) {
        this.prefix = this.router.url.substring(0, this.router.url.indexOf('/friend'));
    }

    ngOnInit() {
        this._searchEl = $(this.searchEl.nativeElement);
        this.search.valueChanges.subscribe((value) => {
            if (this._searchable) {
                this._activeComponent.instance.onSearch(value.trim());
            }
        });
    }

    changeTab(data: NavigationData) {
        this.searchValue = '';
    }

    loadTabComponent(componentRef: ComponentRef<any>) {
        this._activeComponent = componentRef;
        this._searchable = componentRef.instance.onSearch;
        this.placeholder = componentRef.instance.placeholder || 'TÃ¬m kiáº¿m...';
        if (this._searchable) {
            this._searchEl.show();
            componentRef.instance.clearSearchValue = () => {
                this.searchValue = '';
            };
        } else {
            this._searchEl.hide();
        }
    }

}
