import {
    AfterViewInit, Component, ElementRef, EventEmitter, Input, OnDestroy, Output, TemplateRef,
    ViewChild
} from '@angular/core';
import {SocialService} from '../social.service';
import {SocialActivity} from '../model/Activity';
import {BsModalRef, BsModalService} from "ngx-bootstrap";
declare let $: any;

@Component({
    selector: '[socialActivity]',
    templateUrl: './activity.component.html',
    styleUrls: ['./activity.component.css']
})
export class ActivityComponent implements AfterViewInit, OnDestroy {
    @Input() activity: SocialActivity;

    @Output() onUpdated = new EventEmitter<any>();
    @Output() onDeleted = new EventEmitter<any>();

    @ViewChild('policiesDisplay') policiesDisplay: ElementRef;
    @ViewChild('tmpConfirmDelete') tmpConfirmDelete: TemplateRef<any>;

    viewDate: string;
    editing: boolean = false;

    private _modalRef: BsModalRef;

    constructor(
        private socialService: SocialService,
        private modalService: BsModalService
    ) {
        this.viewDate = this.socialService.viewDate;
    }

    ngAfterViewInit() {
        if (this.policiesDisplay) {
            let title = [];
            let maxDisplay = 8;
            for (let i = 0; i < this.activity._policies.length; i++) {
                title.push(this.activity._policies[i].display);
                if (title.length >= maxDisplay) {
                    title.push('vÃ  ' + (this.activity._policies.length - maxDisplay) + ' ngÆ°á»i khÃ¡c...');
                    break;
                }
            }
            $(this.policiesDisplay.nativeElement)['tooltip']({
                title: title.join('<br>'),
                html: true
            });
        }
    }

    ngOnDestroy() {
        this.declineDelete();
    }

    activityEdit() {
        this.editing = true;
    }

    activityCancelEdit() {
        this.editing = false;
    }

    activityDelete() {
        this._modalRef = this.modalService.show(this.tmpConfirmDelete, {class: 'modal-sm'});
    }

    confirmDelete() {
        this.socialService.activityRemove({activity: this.activity.uuid}, (activity: SocialActivity) => {
            if (activity && activity.uuid) {
                this.onDeleted.emit(activity);
            }
        });
    }

    declineDelete() {
        if (this._modalRef) {
            this._modalRef.hide();
            this._modalRef = null;
        }
    }

    activityLike() {
        const params = {
            activity: this.activity.uuid
        };
        this.activity.likeStatus = !this.activity.likeStatus;
        if (this.activity.likeStatus) {
            this.activity.likeCount++;
            this.socialService.activityLike(params, data => {
            });
        } else {
            this.activity.likeCount--;
            this.socialService.activityUnLike(params, data => {
            });
        }
    }

    activityUpdate(data) {
        this.socialService.activityUpdate(data, (activity: SocialActivity) => {
            if (activity && activity.uuid) {
                this.onUpdated.emit(activity);
                this.editing = false;
            }
        });
    }
}
