import {Component, EventEmitter, Output, Input, ViewChild, ElementRef, OnInit} from '@angular/core';
import {SocialComment} from '../model/Comment';
import {AutoSizeDirective} from 'inet-core';
import {FileListItem} from 'inet-ui';
import {SocialService} from "../social.service";
import {ProgressComponent} from "../progress/progress.component";

@Component({
    selector: '[commentPost]',
    templateUrl: './comment-post.component.html',
    styleUrls: ['./comment-post.component.css']
})
export class CommentPostComponent implements OnInit {
    @Input() placeholder: string = 'Ná»i dung tháº£o luáº­n..';
    @Input() commentEdit: SocialComment;

    @Output() onCancel = new EventEmitter<any>();
    @Output() onPost = new EventEmitter<any>();

    @ViewChild('messageEl') messageEl: ElementRef;
    @ViewChild(AutoSizeDirective) messageAutoSize: AutoSizeDirective;
    @ViewChild(ProgressComponent) indicator: ProgressComponent;

    message: string = '';
    files: FileListItem[] = [];
    removeFiles: FileListItem[] = [];

    constructor(private socialService: SocialService) { }

    ngOnInit() {
        if (this.commentEdit) {
            this.editComment(this.commentEdit);
        }
    }

    enterPost(event) {
        event.preventDefault();
        this.doPost();
    }

    doPost() {
        const params = this.getData();
        if (!params.message) {
            return this.focusMessage();
        }
        this.onPost.emit(params);
        this.indicator.show();
    }

    doCancel() {
        this.onCancel.emit(this.commentEdit);
        if (this.commentEdit) {
            this.resetForm();
        }
    }

    getData() {
        let params: any = {
            message: this.message
        };

        for (let i = 0; i < this.files.length; i++) {
            if (!this.files[i].id) {
                params['file-' + i] = this.files[i];
            }
        }

        if (this.commentEdit) {
            params.activity = this.commentEdit.activityID;
            params.comment = this.commentEdit.uuid;
            params.gridfsUUID = this._getRemoveFiles();
        }

        return params;
    }

    editComment(comment: SocialComment) {

        this.resetForm();

        this.commentEdit = comment;

        this.files = this.socialService.attachmentToFiles(comment.attachments);

        this.message = comment.message;

        this.focusMessage();

        // Trigger input to adjust size
        this._resizeInput();

    }

    resetForm() {
        // Clear data
        this.message = '';

        this.files.length = 0;
        this.removeFiles.length = 0;

        this._resizeInput();

        if (this.indicator) {
            this.indicator.hide();
        }
    }

    focusMessage() {
        this.messageEl.nativeElement.focus();
    }

    private _getRemoveFiles(): string {
        return this.removeFiles.filter(file => file.id).map(file => file.id).join(',');
    }

    private _resizeInput() {
        setTimeout(() => {
            this.messageAutoSize.adjust();
        }, 0);
    }
}
