import {Component, ElementRef, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild} from '@angular/core';
import {ListUserComponent} from '../list-user/list-user.component';
import {SocialService} from '../../social.service';

@Component({
    selector: '[socialGroupCreate]',
    templateUrl: './group-create.component.html',
    styleUrls: ['./group-create.component.css']
})
export class GroupCreateComponent implements OnInit, OnDestroy {
    @Output() onCreate = new EventEmitter<any>();

    @ViewChild('modal') modal;
    @ViewChild('inputGroup') inputGroup: ElementRef;
    @ViewChild('listUser') listUser: ListUserComponent;

    private _input;

    constructor(
        private socialService: SocialService
    ) {
    }

    ngOnInit() {
        this._input = this.inputGroup.nativeElement;
        document.body.appendChild(this.modal._element.nativeElement);
    }

    ngOnDestroy() {
        this.modal._element.nativeElement.remove();
    }

    open() {
        this._input.value = '';
        this.listUser.resetData();
        this.modal.show();
    }

    onShown() {
        this._input.focus();
        this.listUser.onSearch();
    }

    onSave() {
        let params = {
            category: this._input.value.trim(),
            member: this.listUser.selectMembers.map(item => item.username).join(',')
        };
        if (!params.category) {
            this._input.focus();
            return;
        }

        if (!params.member) {
            this.listUser.focusInput();
            return;
        }

        this.socialService.groupMemberCreate(params, (data) => {
            this.onCreate.emit(data);
        });

        this.modal.hide();
    }

    onCancel() {
        this.modal.hide();
    }
}
