import {Component, OnDestroy, ViewChild, OnInit, Input} from '@angular/core';
import {SocialService} from '../social.service';
import {ActivityParams} from '../model/ActivityParams';
import {SocialActivity} from '../model/Activity';
import {ActivityPostComponent} from '../activity-post/activity-post.component';
import {Subscription} from "rxjs";

@Component({
    selector: '[socialActivityList]',
    templateUrl: './activity-list.component.html',
    styleUrls: ['./activity-list.component.css']
})
export class ActivityListComponent implements OnInit, OnDestroy {
    @ViewChild('activityPost') activityPost: ActivityPostComponent;
    @Input() postEnable: boolean = true;
    @Input() scroller: HTMLElement | any = document;
    @Input() type: string = '';
    @Input() application: string = '';
    @Input() loader: Function = (params, callback) => this.doLoadWall(params, callback);

    activities: SocialActivity[] = [];
    showLoading: boolean = true;
    hasMore: boolean = false;
    params: ActivityParams = {
        pageSize: 5,
        pageNumber: 0
    };

    private _activityChange: Subscription;
    private _loading: boolean;

    constructor(
        public socialService: SocialService
    ) {
        // Receive activity notify real time
        this._activityChange = this.socialService.activityChange.subscribe(activityId => {
            if (activityId) {
                this.socialService.activityLoad({
                    activity: activityId
                }, (activity: SocialActivity) => {
                    if (activity && activity.uuid) {
                        this._addActivity(activity, true);
                    }
                });
            }
        });
    }

    ngOnInit() {
        this.initScroller();
        this.loadWall(true);
    }

    ngOnDestroy() {
        this.removeEventScroller();
        this._activityChange.unsubscribe();
    }

    removeActivityById(id: string) {
        for (let i = 0; i < this.activities.length; i++) {
            const activity = this.activities[i];
            if (activity.uuid === id) {
                this.activities.splice(i, 1);
                return;
            }
        }
    }

    loadWall(init?: boolean) {

        if (init) {
            this.clearWall();
            this.params.pageNumber = 0;
            this.showLoading = true;

            if (this.type) {
                this.params.type = this.type;
            }

            if (this.application) {
                this.params.application = this.application;
            }

        }

        this._loading = true;

        this.loader(this.params, (data, err) => this._renderWall(data));
    }

    doLoadWall(params, callback) {
        this.socialService.activityWall(params, callback);
    }

    clearWall() {
        this.activities.length = 0;
    }

    private _renderWall(data) {
        if (data) {
            const activities = data['items'] || [];
            activities.forEach(activity => this._addActivity(activity));
            this.hasMore = data['total'] > this.activities.length;
        }

        this.showLoading = false;

        setTimeout(() => {
            this._loading = false;
            this._scrollBottomListener();
        }, 1000);
    }

    activityCreate(params: any) {
        if (params.activity) {
            this.socialService.activityUpdate(params, this._activityPosted.bind(this));
        } else {
            this.socialService.activityPost(params, this._activityPosted.bind(this));
        }
    }

    activityUpdated(activity: SocialActivity) {

        // Replace by activity updated
        for (let i = 0; i < this.activities.length; i++) {
            if (this.activities[i].uuid === activity.uuid) {
                this.activities[i] = activity;
                return;
            }
        }
        this.activities.unshift(activity);
    }

    activityDeleted(activity: SocialActivity) {
        this.removeActivityById(activity.uuid);
    }

    initScroller() {
        this._scrollBottomListener = this._scrollBottomListener.bind(this);
        if (this.scroller) {
            this.initEventScroller(this.scroller);
        }
    }

    scrollToTop() {
        if (this.scroller) {
            this.scroller.scrollTop = 0;
        }
    }

    initEventScroller(scroller: HTMLElement | any) {
        this.scroller = scroller;
        this.scroller.addEventListener('scroll', this._scrollBottomListener);
    }

    removeEventScroller() {
        if (this.scroller) {
            this.scroller.removeEventListener('scroll', this._scrollBottomListener);
        }
    }

    private _scrollBottomListener(e?: any) {
        if (this._loading || !this.hasMore) {
            return;
        }
        const scroller = e && e.target || this.scroller;
        const target = scroller.scrollingElement || scroller;
        if (target) {

            // Scroll bottom to load more
            const bottom = target.scrollHeight - target.scrollTop - target.clientHeight;

            if (bottom < 150) {
                this.params.pageNumber++;
                this.loadWall();
            }
        }
    }

    private _activityPosted(activity: SocialActivity, err: any) {
        if (activity && activity.uuid) {
            this.activityPost.resetForm();
            this.activityPost.hideForm(true);
            this._addActivity(activity, true);
        }
    }

    private _addActivity(activity: SocialActivity, insertAtFirst?: boolean) {
        this.removeActivityById(activity.uuid);
        if (insertAtFirst) {
            this.activities.unshift(activity);
        } else {
            this.activities.push(activity);
        }
    }
}
