import {Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild} from '@angular/core';
import {ListUserComponent} from '../list-user/list-user.component';

@Component({
    selector: '[socialGroupMember]',
    templateUrl: './group-member.component.html',
    styleUrls: ['./group-member.component.css']
})
export class GroupMemberComponent implements OnInit, OnDestroy {
    @Input() title: string = 'ThÃªm thÃ nh viÃªn';
    @Output() onSelect = new EventEmitter<any>();

    @ViewChild('modal') modal;
    @ViewChild('listUser') listUser: ListUserComponent;

    constructor() {
    }

    ngOnInit() {
        document.body.appendChild(this.modal._element.nativeElement);
    }

    ngOnDestroy() {
        this.modal._element.nativeElement.remove();
    }

    open() {
        this.listUser.resetData();
        this.modal.show();
    }

    onShown() {
        this.listUser.focusInput();
        this.listUser.onSearch();
    }

    onSave() {
        this.onSelect.emit(this.listUser.selectMembers);
        this.modal.hide();
    }

    onCancel() {
        this.modal.hide();
    }
}
